/* Global styles */
body {
  font-family: 'Roboto', Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #fbede2;
}

.nav-links a {
  text-decoration: none;
  color: black;
  margin: 0 10px;
  position: relative;
}

.nav-links a.active::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 2px;
  background-color: black;
}

.nav-links a:hover {
  text-decoration: underline;
}

input::placeholder {
  color: #000000;
}

